<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12">
        <v-data-table
          :headers="headers"
          :items="users"
          class="elevation-8"
          style="background-color: rgba(255, 255, 255, 0.2)"
        >
          <template v-slot:top>
            <v-toolbar flat style="background-color: rgba(255, 255, 255, 0.2)">
              <v-toolbar-title>用户信息管理</v-toolbar-title>
              <v-divider class="mx-4" inset vertical></v-divider>
              <v-spacer></v-spacer>
              <!--对话框，用于修改或者新建-->
              <v-dialog v-model="dialog" max-width="600px">
                <template v-slot:activator="{ on, attrs }">
                  <!--点击这个按钮的时候，将状态切换为创建-->
                  <v-btn
                    color="blue lighten-4"
                    class="mb-2"
                    v-bind="attrs"
                    v-on="on"
                    @click="state.create = true"
                  >
                    New User
                  </v-btn>
                </template>
                <!-- 创建和编辑的弹窗，判断删除还是编辑 -->
                <v-card>
                  <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                  </v-card-title>
                  <!-- 表单内容 -->
                  <v-card-text>
                    <v-form
                      ref="userForm"
                      v-model="valid"
                      lazy-validation
                      autocomplete="off"
                    >
                      <!--用户名和密码-->
                      <v-row>
                        <v-col cols="12" sm="6">
                          <v-text-field
                            v-model="editedItem.mk_username"
                            :rules="rules.mk_usernameRules"
                            label="mk_username"
                            @blur="usernameBlur"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <v-text-field
                            type="mk_password"
                            v-model="editedItem.mk_password"
                            :rules="rules.mk_passwordRules"
                            label="mk_password"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <!--用户的状态，用以禁用用户-->
                      <v-row>
                        <v-col cols="12">
                          <v-radio-group v-model="editedItem.mk_enabled" row>
                            <v-radio
                              label="Unable"
                              color="red darken-4"
                              :value="-1"
                            ></v-radio>
                            <v-radio
                              label="Able"
                              color="pink"
                              :value="0"
                              readonly
                            ></v-radio>
                            <!-- logging属性设置为禁用，不能创造正在在线的用户 -->
                            <v-radio
                              label="Logging"
                              color="blue lighten-2"
                              :value="1"
                            ></v-radio>
                            <v-radio
                              label="Admin"
                              color="green lighten-1"
                              :value="2"
                            ></v-radio>
                          </v-radio-group>
                        </v-col>
                      </v-row>
                      <!--用户邮箱-->
                      <v-row>
                        <v-col cols="12">
                          <v-text-field
                            v-model="editedItem.mk_email"
                            :rules="rules.mk_emailRules"
                            label="mk_email"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-form>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <!-- 表单canel按钮 -->
                    <v-btn color="red darken-4" text @click="close"
                      >Cancel</v-btn
                    >
                    <!--判断是创建还是更改,替换card右下角按钮-->
                    <!-- 创建 -->
                    <v-btn
                      v-if="state.create"
                      :disabled="!valid"
                      color="blue darken-1"
                      text
                      @click="create"
                      >Create</v-btn
                    >
                    <!-- 更改 -->
                    <v-btn
                      v-if="state.update"
                      :disabled="!valid"
                      color="blue darken-1"
                      text
                      @click="update"
                      >Update</v-btn
                    >
                  </v-card-actions>
                </v-card>
              </v-dialog>
              <!--删除对话框，用以再次确认-->
              <v-dialog v-model="dialogDelete" max-width="500px">
                <v-card>
                  <v-card-title class="headline"
                    >Are you sure you want to delete this item?</v-card-title
                  >
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="closeDelete()"
                      >Cancel</v-btn
                    >
                    <v-btn
                      color="blue darken-1"
                      text
                      @click="deleteItemConfirm()"
                      >OK</v-btn
                    >
                    <v-spacer></v-spacer>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <!-- 每个item右侧用于编辑和删除的按钮 -->
          <template v-slot:[`item.actions`]="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">
              mdi-pencil
            </v-icon>
            <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
          </template>
          <!-- 点击reset按钮重新获取数据 -->
          <template v-slot:no-data>
            <v-btn color="red lighten-4" @click="initialize"> Set </v-btn>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
    <!--错误提示，用于反馈创建、修改用户时的错误-->

    <v-snackbar v-model="snackbar">
      {{ snackbarData }}
      <template v-slot:action="{ attrs }">
        <v-btn color="red" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
export default {
  name: "UserAdmin",
  data: () => ({
    state: {
      create: false,
      update: false,
    },
    valid: true,
    dialog: false,
    dialogDelete: false,
    // 用于控制消息条是否显示
    snackbar: false,
    // 表单填写的内容
    snackbarData: "",

    headers: [
      {
        text: "ID",
        align: "start",
        sortable: true,
        value: "mk_id",
      },
      {
        text: "username",
        align: "start",
        sortable: false,
        value: "mk_username",
      },
      {
        text: "email",
        sortable: false,
        value: "mk_email",
      },
      {
        text: "enabled",
        value: "mk_enabled",
      },
      {
        text: "Actions",
        value: "actions",
        sortable: false,
      },
    ],

    users: [],
    editedItem: {
      mk_id: 0,
      mk_username: "",
      mk_password: "",
      mk_enabled: 0,
      mk_email: "",
      icon: null,
    },
    //用于快速向editItem赋默认值
    defaultItem: {
      mk_id: 0,
      mk_username: "",
      mk_password: "",
      mk_enabled: 0,
      mk_email: "",
      icon: null,
    },
    rules: {
      mk_usernameRules: [
        (v) => !!v || "mk_username is none!",
        (v) =>
          (v.length >= 3 && v.length <= 20) ||
          "mk_username must be 3-20 characters!",
      ],
      mk_passwordRules: [
        (v) => !!v || "mk_password is none!",
        (v) =>
          (v.length >= 6 && v.length <= 20) ||
          "mk_password must be 6-20 characters!",
      ],
      mk_emailRules: [
        (v) => !!v || "mk_email is none!",
        (v) => /(\w+)@(\w+)\.(\w+)/.test(v) || "mk_email is not valid!",
      ],
    },
  }),
  computed: {
    formTitle() {
      return this.state.create ? "New User" : "Edit User";
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  methods: {
    initialize() {
      // 获取全部用户,赋值
      this.axios
        .post(this.targetIP() + "/userManage.GetUserList", { admin: "admin" })
        .then((response) => {
          console.log(response);
          // 设置用户列表
          const data = response.data;
          this.users = data;
        })
        .catch((error) => {
          console.log(error);
        });
    },

    editItem(item) {
      // 打开用户编辑框

      this.editedItem = Object.assign({}, item);
      this.dialog = true;
      this.state.update = true;
    },
    deleteItem(item) {
      //打开删除确认框
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },
    deleteItemConfirm() {
      //在删除窗口确认时调用，即在确定窗口点击confirm
      console.log("shanchu");
      this.axios
        .post(this.targetIP() + "/userManage.GetUserList/action", {
          methodname: "deleteUser",
          username: this.editedItem.mk_username,
          admin: "admin",
        })
        .then((response) => {
          console.log(response);
          this.initialize();
        })
        .catch((error) => {
          console.log(error);
        });

      this.closeDelete();
    },
    close() {
      //点击表单canel按钮
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
      });
      this.state.create = false;
      this.state.update = false;
    },
    closeDelete() {
      //关闭删除确定窗，即在确定窗口点击canel
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
      });
    },
    create() {
      //创建用户调用
      this.axios
        .post(this.targetIP() + "/userManage.GetUserList/action", {
          methodname: "createUser",
          username: this.editedItem.mk_username,
          password: this.editedItem.mk_password,
          enabled: this.editedItem.mk_enabled,
          email: this.editedItem.mk_email,
          admin: "admin",
        })
        .then((response) => {
          console.log(response);
          this.initialize();
        })
        .catch((error) => {
          console.log(error);
        });
      this.close();
    },
    update() {
      //更改用户信息调用
      this.axios
        .post(this.targetIP() + "/userManage.GetUserList/action", {
          methodname: "editUser",
          id: this.editedItem.mk_id,
          username: this.editedItem.mk_username,
          password: this.editedItem.mk_password,
          enabled: this.editedItem.mk_enabled,
          email: this.editedItem.mk_email,
          admin: "admin",
        })
        .then((response) => {
          console.log(response);
          if (response.data == "success") {
            this.initialize();
            this.snackbarData = "修改成功";
            this.snackbar = true;
          } else {
            this.snackbarData = response.data;
            this.snackbar = true;
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    usernameBlur() {
      // axios发送post请求
      this.axios
        .post(this.targetIP() + "/verify.register/checkduplicate", {
          username: this.editedItem.mk_username,
        })
        .then((response) => {
          console.log(response);
          console.log(response.data);
          if (response.data == false) {
            this.snackbarData = "用户名已存在，请再想一个";
            this.snackbar = true;
          } else if (response.data == "methoderror") {
            this.snackbarData = "MethodError";
            this.snackbar = true;
          } else if (response.data == true) {
          }
        })
        .catch((error) => {
          // 发生请求错误，错误可能是：1.因为用户名已存在 2.提交的数据不符合要求 3.服务器内部错误
          console.log(error);
          // 显示错误消息
          this.snackbarData = "与服务器连接发生了位置错误";
          this.snackbar = true;
        });
    },
    created() {
      this.initialize();
    },
    targetIP() {
      return this.$store.state.targetIP;
    },
  },
};
</script>
